body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: #FFFFF8;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: #a55a94;
  position: relative;
}

.App-contact, .App-about, .App-portfolio {
  margin: auto;
  width: 100%;
}

*, :after, :before {
  box-sizing: revert;
}

a {
  text-decoration: none;
  color: #81277a;
}

.center {
  margin: auto;
  justify-content: center;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-size: revert;
  line-height: revert;
}

input:focus,
textarea:focus,
select:focus,
input,
textarea,
button,
select,
a,
a:focus,
a:visited, 
a:active {
  -webkit-tap-highlight-color: transparent;
  outline: none;
  color: #81277a;
}

.small-text {
  font-size: small;
}

.wide-100 {
  width: 100%;
}

.pointer {
  cursor: pointer;
}
.Header-wrapper {
    font-size: 15px;
    position: fixed;
    background-color: #FFFFF8;
    top: 0;
    width: 100%;
    height: 90px;
    margin: auto;
}

.Header-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    padding: 30px;
    width: 40%;
    list-style-type: none;
    font-family: 'Chivo', sans-serif;
}

.Header-nav-mobile {
    display: none;
}

.Header-noclick {
    pointer-events: none;
}

a[Header-noclick] {
    pointer-events: none;
 }

.Header-noclick:hover {
    color: #a55a94;
    filter: drop-shadow(0 0 0 rgba(255, 212, 239, 0));
}

.heart1 {
    transition: all 0.30s;
    height: 10px;
    width: 10px;
    filter: invert(48%) sepia(13%) saturate(1880%) hue-rotate(261deg) brightness(86%) contrast(86%);
}

.heart0 {
    transition: all 0.30s;
    height: 10px;
    width: 10px;
    filter: invert(100%) sepia(41%) saturate(2%) hue-rotate(238deg) brightness(106%) contrast(100%);
}

.Header-item:hover {
    color: #a55a94;
    filter: drop-shadow(1px 1px 1px rgb(255, 212, 239));
}

@media screen and (min-width: 300px) and (max-width: 650px){

    .Header-wrapper {
        height: 60px;
        position: absolute;
    }

    .Header-item {
        padding: 5px;
    }

    .Header-nav {
        display: none;
    }

    .HeaderNav-wrapper {
        background-color: #FFFFF8;
        padding: 20px;
    }

    .HeaderNav-button {
        padding-top: 20px;
    }

    .Header-nav-mobile {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: auto;
        padding: 10px 30px;
        list-style-type: none;
        font-family: 'Chivo', sans-serif;
        transition: transform 0.6s ease-in-out;
        position: -webkit-sticky;
        position: sticky;
        z-index: 2;
        background-color: #FFFFF8;
    }

}
