.Admin-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 130px auto 78px auto;
    padding: 10px;
    color: #a55a94;
}

.Admin-button-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    padding: 20px;
}

.Admin-title, .AdminLogin-title {
    font-family: 'Bungee Shade', cursive;
    font-size: 28px;
    margin: 5px auto 30px auto;
    color: #81277a;
}

.AdminLogin-title {
    margin: auto auto 20px auto;
}

.Admin-button {
    width: 120px;
    height: 50px;
    padding: 10px;
    margin: 10px;
    border: solid 1px  #a55a94;
    background-color: #D7FAFF;
    box-shadow: 4px 4px 1px #EABEE0;
    cursor: pointer;
}

.Admin-button:hover {
    background-color: #eabee085;
    transition: all 0.20s;
}

.Admin-button:active {
    box-shadow: 1px 1px 1px #EABEE0;
}

.Admin-input, .Admin-select {
    width: 200px;
    margin: 10px;
    padding: 10px;
    border: solid 1px  #a55a94;
    background-color: #EABEE0;
    color: #a55a94;
}

.Admin-textarea {
    width: 442px;
    height: 50px;
}

.Admin-input-long {
    width: 442px;
}

.Admin-select {
    width: 222px;
}

.Admin-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Admin-submit {
    font-size: 14px;
}

.AdminLogin-page {
    background-color: #b5e1e7;
}

.AdminLogin-form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 0 30px;
    padding: 10px;
    color: #a55a94;
}

.AdminLogin-enter {
    transition: transform 1s ease-in;
}

@media screen and (min-width: 300px) and (max-width: 650px){

    .Admin-wrapper {
        margin: 70px auto auto auto;
    }

    .Admin-button-wrapper {
        justify-content: center;
        margin: 0;
        padding: 20px;
        flex-wrap: wrap;
    }
    
    .Admin-title {
        font-size: 5vw;
    }
    
    .Admin-textarea {
        width: 200px;
    }
    
    .Admin-input-long {
        width: 200px;
    }
    
    .Admin-section {
        justify-content: center;
        flex-direction: column;
    }

}
.Portfolio-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    transition: transform 0.6s ease-in-out;
}

.carousel {
    width: 800px;
}

.carousel-inner {
    padding-bottom: 20px;
}

.PortfolioBox-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.PortfolioBox-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.PortfolioBox-main-box {
    width: 600px;
    height: 445px;
    margin: 15px;
    background: #D7FAFF no-repeat center top;
    box-shadow: 6px 6px 1px #EABEE0;
    overflow: hidden;
    background-size: cover;
    border: solid 1px  #a55a94;
}

.PortfolioBox-description-box {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 572px;
    height: 150px;
    margin: 0 15px 0 27px;
    padding: 15px;
    background: #f0d3e9;
    font-size: 14px;
}

.PortfolioBox-p, .PortfolioBox-p-s {
    margin: 5px;
    text-align: left;
}

.PortfolioBox-p-s {
    font-size: 12px;
    font-weight: bold;
}

.PortfolioBox-h3 {
    width: 560px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 5px 5px 5px;
    font-size: 24px;
    font-family: 'Dancing Script', cursive;
    color: #81277a;
}

.PortfolioBox-date {
    font-size: small;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
}

.carousel-indicators [data-bs-target] {
    background-color: #81277a;
}

.carousel-indicators {
    margin-bottom: -5px;
}

.carousel-control-next-icon {
    background-image: url(/static/media/right-arrow.f7f271f3.svg);
    filter: invert(19%) sepia(20%) saturate(6096%) hue-rotate(279deg) brightness(100%) contrast(93%);
}

.carousel-control-prev-icon {
    background-image: url(/static/media/left-arrow.96679402.svg);
    filter: invert(19%) sepia(20%) saturate(6096%) hue-rotate(279deg) brightness(100%) contrast(93%);
}

.carousel-control-next, .carousel-control-prev {
    bottom: 200px;
}


@media screen and (min-width: 300px) and (max-width: 800px){

    .carousel {
        width: 100%;
    }

    .carousel-control-next-icon, .carousel-control-prev-icon {
        width: 1.5rem;
    }

    .carousel-control-next, .carousel-control-prev {
        margin: 0 -12px;
        bottom: 239px;
    }

    .PortfolioBox-main-box {
        width: 300px;
        height: 250px;
        margin: 15px;
    }

    .PortfolioBox-description-box {
        width: 270px;
        height: 198px;
        margin: 0 15px 0 27px;
        font-size: 3.5vw;
    }

    .PortfolioBox-p-s {
        font-size: 0.6rem;
    }

    .PortfolioBox-p {
        font-size: 0.8rem;
    }


    .PortfolioBox-h3 {
        width: 260px;
    }

    .carousel.item-active {
        
    }
}
.About-wrapper {
    background: #D7FAFF;
    width: 70%;
    min-height: 400px;
    padding: 30px;
    margin: auto;
    transition: transform 0.6s ease-in-out;
}

.About-border {
    border: solid 1px  #a55a94;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto; 
    min-height: 400px;
}

.About-photo {
    padding: 40px;
}

.About-photo img, .About-photo picture {
    height: 160px;
    width: 160px;
    border-radius: 100%;
}

.About-text {
    padding: 30px;
    text-align: left;
    font-size: 22px;
    font-family: 'Spartan', sans-serif;
}


@media screen and (min-width: 300px) and (max-width: 800px){

    .About-wrapper {
        margin: 90px auto auto auto;
    }

    .About-border {
        flex-direction: column;
        padding: 0;
        margin: auto;
    }
    
    .About-photo {
        padding-bottom: 20px !important;
    }
    
    .About-text {
        padding: 20px;
        text-align: left;
        font-size: 1rem;
    }

}
